@font-face {
  font-family: "Roboto";
  src: local(Roboto Thin), url("../fonts/Roboto-Thin.woff2") format("woff2"), url("../fonts/Roboto-Thin.woff") format("woff");
  font-weight: 100;
}

@font-face {
  font-family: "Roboto";
  src: local(Roboto Light), url("../fonts/Roboto-Light.woff2") format("woff2"), url("../fonts/Roboto-Light.woff") format("woff");
  font-weight: 300;
}

@font-face {
  font-family: "Roboto";
  src: local(Roboto Regular), url("../fonts/Roboto-Regular.woff2") format("woff2"), url("../fonts/Roboto-Regular.woff") format("woff");
  font-weight: 400;
}

@font-face {
  font-family: "Roboto";
  src: local(Roboto Medium), url("../fonts/Roboto-Medium.woff2") format("woff2"), url("../fonts/Roboto-Medium.woff") format("woff");
  font-weight: 500;
}

@font-face {
  font-family: "Roboto";
  src: local(Roboto Bold), url("../fonts/Roboto-Bold.woff2") format("woff2"), url("../fonts/Roboto-Bold.woff") format("woff");
  font-weight: 700;
}

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

html {
  background: #1a1a1a;
}

body {
  min-height: 350px;

  background: #1a1a1a;
  color: #FFFFFF;
  font-weight: 300;
  font-family: Helvetica, Roboto;
  font-size: 20px;
}

.container {
  padding-left: 10px;
  padding-right: 10px;
}

.page {
  padding-top: 47px;
}

.parsel-links {
  display: none;
  a {
    display: block;
    color: #ffffff;
  }
}

.btn {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  justify-content: center;

  width: 155px;
  height: 50px;
  margin-top: 15px;

  border: none;
  font-weight: 300;
  border-radius: 6px;
  background: #333333;
  font-size: 18px;
  color: #FFFFFF;
  letter-spacing: 0;
  text-align: center;
  text-decoration: none;
}

.btn__blue {
  background: #4A90E2;
}

.btn__red {
  background: #A0453F;
} 

.disabled {
  pointer-events: none;
  cursor: auto;
}

.is-hidden {
  display: none;
}

.splash {
  position: fixed;
  left: 0;
  top: 0;

  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;

  width: 100%;
  height: 100%
}

.splash__logo {
  width: 121px;
  height: 114px;
  background: url('../images/logo.svg') no-repeat center/contain;
  margin-bottom: 17px;
}

.splash__text {
  font-family: Roboto-Regular;
  font-size: 24px;
  color: #FFFFFF;
  letter-spacing: 0;
  text-align: center;
}